@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@500;700&display=swap);
*{margin:0;padding:0}

*{font-family:'Manrope';font-size:13px}.container{background-color:rgba(158,175,194,0.4);width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}.card{background-color:#ecf2f8;width:80%;display:flex;border-radius:10px;box-shadow:-1px -1px 15px -1px rgba(158,175,194,0.4);z-index:100}.content{padding:30px}.image{width:40%;border-top-left-radius:10px;border-bottom-left-radius:10px}.title{font-weight:bold;font-size:1.9em;color:#48556a}.name{font-weight:bold;color:#48556a}.text,.date{margin-top:15px;color:#6d7f97}.item__image>img.image{width:50px;height:50px;border-radius:50%}.item{display:flex;margin-top:15px;width:100%}.item__content{width:100%;display:flex;justify-content:space-between;align-items:center;margin-left:10px;margin-right:10px}button{width:45px;height:45px;border-radius:50%;border:none;background-color:rgba(158,175,194,0.4)}.share::after{width:55px;height:55px;content:url(/article-preview-component-master/static/media/icon-share.890102fe.svg)}

